import { useState } from "react";
import GetStarted from "../../GetStarted";
import AuthCardWrapper from "../../AuthCardWrapper";
import style from "../../Auth.module.css";
import AuthFormFieldWithIcon from "../../../Ui/Auth/AuthFormFieldWithIcon";
import ButtonWithTypeSubmit from "../../../Ui/Button/ButtonWithTypeSubmit";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import { assestsAuthUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import OtpResendTimer from "../../Verification/OtpResendTimer";
import {
  useAuthInstituteNumber,
  useAuthInstituteNumberVerify,
} from "../../../Hooks/ApiHooks/Auth/auth-api";
import { decryption } from "../../../Services/Encryption/decrypt";
import { useLocation, useNavigate } from "react-router-dom";

const InstituteOtpVerification = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [state, setState] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });

  const [authInstituteNumberVerify] = useAuthInstituteNumberVerify();
  const [authInstituteNumber] = useAuthInstituteNumber();

  const onChangeHandler = (e) => {
    setState(e.target.value);
  };
  const onSubmitHandler = (e) => {
    e.preventDefault();
    if (state && getQuery.state?.phoneNumber) {
      setDisabled((pre) => !pre);

      authInstituteNumberVerify({
        insPhoneNumber: `${getQuery.state?.phoneNumber}`,
        verify: {
          insOtpCode: state,
          insPhoneNumber: `${getQuery.state?.phoneNumber}`,
        },
      })
        .then((res) => {
          let response = res?.data;
          setDisabled((pre) => !pre);
          if (response?.message === "Invalid OTP") {
            setSnackbar({
              msg: response?.message,
              open: true,
              variant: "error",
            });
          } else {
            navigate("/institute/create/account", {
              state: {
                phoneNumber: response?.id,
                insMobileStatus: response?.insMobileStatus,
              },
            });
          }
        })
        .catch((err) => {
          setSnackbar({
            msg: `Time out of request with reason ${err}`,
            open: true,
            variant: "error",
          });
          setDisabled((pre) => !pre);
        });
    } else {
      setErrorField({
        insOtpCode: "* required",
      });
    }
  };

  const onOtpResend = () => {
    if (`${getQuery.state?.phoneNumber}`) {
      authInstituteNumber({
        phone: {
          insPhoneNumber: `${getQuery.state?.phoneNumber}`,
          status: "Not Verified",
        },
      })
        .then((res) => {
          let response = decryption(res?.data?.encrypt);
          if (response?.insPhoneNumber) {
            setSnackbar({
              open: true,
              msg: `Code will be resent to xxxx- ${`${getQuery.state?.phoneNumber}`?.substring(
                `${getQuery.state?.phoneNumber}`?.length - 4
              )}`,
              variant: "success",
            });
          }
        })
        .catch(() => {
          setSnackbar({
            open: true,
            msg: "Time out of request...",
            variant: "error",
          });
        });
    }
  };
  return (
    <GetStarted>
      <AuthCardWrapper
        title="verification_code"
        subTitle={`${t("verification_code_note")}mobile number${t(
          "ending"
        )} xxxx - ${`${getQuery.state?.phoneNumber}`?.substring(
          `${getQuery.state?.phoneNumber}`?.length - 4
        )}`}
        subTitleStyle={{
          fontSize: "0.8rem",
        }}
      >
        <form onSubmit={onSubmitHandler} className={style.auth_login_form}>
          <AuthFormFieldWithIcon
            placeholder={t("verification_code")}
            fieldIconUrl={`${assestsAuthUrl}/auth-otp.svg`}
            maxLength="4"
            validate={errorField["userOtpCode"]}
            name="userOtpCode"
            value={state}
            onChange={onChangeHandler}
          />

          <div className={style.auth_footer_container}>
            <p className={style.auth_otp_text}>
              {t("if_you_did_not_recieve_otp")}
              <OtpResendTimer onOtpResend={onOtpResend} />
            </p>
          </div>

          <ButtonWithTypeSubmit
            type="submit"
            buttonText={t("verify")}
            disabled={disabled}
          />

          <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
        </form>
      </AuthCardWrapper>
    </GetStarted>
  );
};

export default InstituteOtpVerification;
